.navbar {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background-color: #393232;
    padding: 10px 20px;
  }
  
  .navbar-brand {
    display: flex;
    align-items: center;
    color: white;
    font-size: 1.5rem;
  }
  
  .navbar-brand i {
    margin-right: 10px;
  }
  
  .firm-name {
    font-weight: bold;
  }
  
  .navbar-links {
    list-style-type: none;
    display: flex;
    gap: 20px;
  }
  
  .navbar-links li {
    display: inline;
  }
  
  .navbar-links a {
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
  }
  
  .navbar-links a:hover {
    text-decoration: underline;
  }
  
  .navbar-search {
    display: flex;
  }
  
  .navbar-search input {
    padding: 5px;
    font-size: 1rem;
  }
  
  .navbar-search button {
    padding: 5px 10px;
    background-color: #555;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  .navbar-search button:hover {
    background-color: #777;
  }
  